const logger = {
    ok: (message, ...args) => console.log(`%c${message}`, 'color: green', ...args),
    error: (message, ...args) => console.log(`%c${message}`, 'color: red', ...args),
    warn: (message, ...args) => console.warn(`%c${message}`, 'color: orange', ...args),
    debug: 
    // @ts-ignore
    process.env.NODE_ENV === 'production'
        ? () => null
        : (message, ...args) => console.debug(`%c${message}`, 'color: blue', ...args),
};
export default logger;
